export default function EndScene({ onComplete }) {
  const { user } = useGameStore();
  const [countdown, setCountdown] = useState(5); // 5ì´ˆë¡œ ì¦ê°€
  const [showAllLanguages, setShowAllLanguages] = useState(false);
  
  const userLang = user?.language || "en";
  const messages = THANK_YOU_MESSAGES[userLang] || THANK_YOU_MESSAGES.en;

  useEffect(() => {
    // 2ì´ˆ í›„ ëª¨ë“  ì–¸ì–´ í‘œì‹œ
    const showAllTimer = setTimeout(() => {
      setShowAllLanguages(true);
    }, 2000);

    // ì¹´ìš´íŠ¸ë‹¤ìš´ íƒ€ì´ë¨¸
    const countdownTimer = setInterval(() => {
      setCountdown((prev) => {
        if (prev <= 1) {
          clearInterval(countdownTimer);
          if (onComplete) {
            onComplete();
          }
          return 0;
        }
        return prev - 1;
      });
    }, 1000);

    return () => {
      clearTimeout(showAllTimer);
      clearInterval(countdownTimer);
    };
  }, [onComplete]);

  return (
    <div className="end-scene fullscreen-ad">
      {/* ë©”ì¸ ê°ì‚¬ ë©”ì‹œì§€ (ì‚¬ìš©ì ì–¸ì–´) */}
      <div className={`main-message ${showAllLanguages ? 'slide-up' : ''}`}>
        <div className="celebration-emoji">ğŸ‰</div>
        <h1 className="main-title">{messages.title}</h1>
        <div className="main-thank-you">
          <p className="message-line">{messages.message1}</p>
          <p className="message-line highlight">{messages.message2}</p>
          <p className="message-line highlight">{messages.message3}</p>
          <p className="message-line">{messages.message4}</p>
          <p className="gratitude-text">{messages.gratitude}</p>
        </div>
      </div>

      {/* ëª¨ë“  ì–¸ì–´ ê°ì‚¬ ë©”ì‹œì§€ (ì „ë©´ ê´‘ê³  ìŠ¤íƒ€ì¼) */}
      {showAllLanguages && (
        <div className="all-languages-container">
          <div className="languages-grid">
            {Object.entries(THANK_YOU_MESSAGES).map(([lang, msg]) => (
              <div 
                key={lang} 
                className={`language-card ${lang === userLang ? 'active-lang' : ''}`}
              >
                <div className="lang-code">{lang.toUpperCase()}</div>
                <h3 className="lang-title">{msg.title}</h3>
                <p className="lang-gratitude">{msg.gratitude}</p>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* í•˜ë‹¨ ê³ ì • ì˜ì—­ */}
      <div className="bottom-fixed">
        <div className="auto-redirect">
          <p className="redirect-text">
            {countdown > 0 ? (
              <>
                <span className="countdown">{countdown}</span>
                {messages.redirectText}
              </>
            ) : (
              messages.redirecting
            )}
          </p>
        </div>

        <div className="end-buttons">
          {onComplete && (
            <button className="end-btn primary" onClick={onComplete}>
              {messages.viewLeaderboard}
            </button>
          )}

          <button
            className="end-btn secondary"
            onClick={() => window.location.reload()}
          >
            {messages.returnHome}
          </button>
        </div>
      </div>
    </div>
  );
}
